// Types
interface ContactField {
  id: string
  name: string
  label: string
  type: 'text' | 'email' | 'phone' | 'number' | 'date' | 'select' | 'multiselect' | 'textarea' | 'checkbox' | 'url'
  required: boolean
  showInList: boolean
  showInForm: boolean
  isSystem: boolean
  options?: string[]
  order: number
}

interface ContactStatus {
  id: string
  name: string
  color: string
  description?: string
  isDefault: boolean
  order: number
}

interface ContactSource {
  id: string
  name: string
  icon: string
  isActive: boolean
}

interface LeadScoringRule {
  id: string
  name: string
  condition: string
  points: number
  isActive: boolean
}

interface ImportExportSettings {
  defaultImportMapping: Record<string, string>
  exportFormat: 'csv' | 'xlsx' | 'json'
  includeCustomFields: boolean
  includeActivityHistory: boolean
}

