  const [scoringRules, setScoringRules] = useState<LeadScoringRule[]>(mockScoringRules)
  
  // Modal states
  const [showFieldModal, setShowFieldModal] = useState(false)
  const [showStatusModal, setShowStatusModal] = useState(false)
  const [showSourceModal, setShowSourceModal] = useState(false)
  const [showScoringModal, setShowScoringModal] = useState(false)
  const [editingField, setEditingField] = useState<ContactField | null>(null)
  const [editingStatus, setEditingStatus] = useState<ContactStatus | null>(null)
  const [editingSource, setEditingSource] = useState<ContactSource | null>(null)
  const [editingScoringRule, setEditingScoringRule] = useState<LeadScoringRule | null>(null)

  // General settings
  const [generalSettings, setGeneralSettings] = useState({
    defaultView: 'grid' as 'grid' | 'list',
    itemsPerPage: 20,
    enableDuplicateDetection: true,
    duplicateCheckFields: ['email', 'phone'],
    autoMerge: false,
    enableActivityTracking: true,
    retentionDays: 30,
    enableGDPR: true,
    requireEmailOptIn: true,
  })

  // Import/Export settings
  const [importExportSettings, setImportExportSettings] = useState<ImportExportSettings>({
    defaultImportMapping: {},
    exportFormat: 'csv',
    includeCustomFields: true,
    includeActivityHistory: false,
  })

  const tabs = [
    { id: 'fields' as const, label: 'Contact Fields', icon: 'ðŸ“' },
    { id: 'statuses' as const, label: 'Statuses', icon: 'ðŸ·ï¸' },
    { id: 'sources' as const, label: 'Lead Sources', icon: 'ðŸ“Š' },
    { id: 'scoring' as const, label: 'Lead Scoring', icon: 'â­' },
    { id: 'import-export' as const, label: 'Import/Export', icon: 'ðŸ“¥' },
    { id: 'general' as const, label: 'General', icon: 'âš™ï¸' },
  ]

  const colorOptions = [
    { id: 'blue', class: 'bg-blue-500' },
    { id: 'amber', class: 'bg-amber-500' },
    { id: 'purple', class: 'bg-purple-500' },
    { id: 'emerald', class: 'bg-emerald-500' },
    { id: 'slate', class: 'bg-slate-500' },
    { id: 'red', class: 'bg-red-500' },
    { id: 'pink', class: 'bg-pink-500' },
    { id: 'cyan', class: 'bg-cyan-500' },
  ]

  const fieldTypes = [
    { id: 'text', label: 'Text' },
    { id: 'email', label: 'Email' },
    { id: 'phone', label: 'Phone' },
    { id: 'number', label: 'Number' },
    { id: 'date', label: 'Date' },
    { id: 'select', label: 'Dropdown' },
    { id: 'multiselect', label: 'Multi-select' },
    { id: 'textarea', label: 'Text Area' },
    { id: 'checkbox', label: 'Checkbox' },
    { id: 'url', label: 'URL' },
  ]

  // Toggle field visibility
  const toggleFieldVisibility = (fieldId: string, property: 'showInList' | 'showInForm') => {
    setFields(fields.map(f => 
      f.id === fieldId ? { ...f, [property]: !f[property] } : f
    ))
  }

  // Toggle field required
  const toggleFieldRequired = (fieldId: string) => {
    setFields(fields.map(f => 
      f.id === fieldId && !f.isSystem ? { ...f, required: !f.required } : f
    ))
  }

  // Delete custom field
  const deleteField = (fieldId: string) => {
    setFields(fields.filter(f => f.id !== fieldId))
  }

  // Toggle source active
  const toggleSourceActive = (sourceId: string) => {
    setSources(sources.map(s => 
      s.id === sourceId ? { ...s, isActive: !s.isActive } : s
    ))
  }

  // Toggle scoring rule active
  const toggleScoringRuleActive = (ruleId: string) => {
    setScoringRules(scoringRules.map(r => 
      r.id === ruleId ? { ...r, isActive: !r.isActive } : r
    ))
  }

  // Delete scoring rule
  const deleteScoringRule = (ruleId: string) => {
    setScoringRules(scoringRules.filter(r => r.id !== ruleId))
  }

  // Set default status
  const setDefaultStatus = (statusId: string) => {
    setStatuses(statuses.map(s => ({
      ...s,
      isDefault: s.id === statusId
    })))
  }

  // Delete status
  const deleteStatus = (statusId: string) => {
    const status = statuses.find(s => s.id === statusId)
    if (status?.isDefault) {
      alert('Cannot delete the default status')
      return
    }
    setStatuses(statuses.filter(s => s.id !== statusId))
  }

